.container {
    position: relative;
    width: 8em;
    /* min-height: 1.5em;
    max-height: 3em; */
    height: 2.5em;
    border: .05em solid #ccc;
    display: flex;
    align-items: center;
    gap: .5em;
    padding: 0 .5em;
    border-radius: .25em;
    outline: none;
    /* margin-top:16px; */
  }
  
  .container:focus {
    border-color: var(--dark-blue);
  }
  
  .value {
    flex-grow: 1;
    display: flex;
    gap: .5em;
    flex-wrap: wrap;
  }
  
  .clear-btn {
    background: none;
    color: #777;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
    font-size: 1.25em;
  }
  
  .clear-btn:focus,
  .clear-btn:hover {
    background: none;
    color: #333;
  }
  
  .divider {
    background-color: #777;
    align-self: stretch;
    width: .05em;
  }
  
  .caret {
    translate: 0 25%;
    border: .25em solid transparent;
    border-top-color: #777;
  }

  .options {
    position: absolute;
    margin: 0;
    padding: 0;
    list-style: none;
    display: none;
    max-height: 15em;
    overflow-y: auto;
    border: .05em solid #777;
    border-radius: .25em;
    width: 100%;
    left: 0;
    top: calc(100% + .25em);
    background-color: white;
    z-index: 100;
  }

  .options.show {
    display: block;
  }

  .option {
    padding: .25em .5em;
    cursor: pointer;
  }
  
  .option.selected {
    background-color: hsl(200, 100%, 70%);
  }
  
  .option.highlighted {
    background-color: hsl(200, 100%, 50%);
    color: white
  }